import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/Home.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },    
    
  },
  {
    path: '/rentcar',
    name: 'rentcarMain',
    component: () => import('@/views/rentcarMain.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },
  },
	{
    path: '/stay',
    name: 'stayMain',
    component: () => import('@/views/stayMain.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },
  },
	{
    path: '/food',
    name: 'foodMain',
    component: () => import('@/views/foodMain.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },
  },
	{
    path: '/cafe',
    name: 'cafeMain',
    component: () => import('@/views/cafeMain.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },
  },
	{
    path: '/trip',
    name: 'tripMain',
    component: () => import('@/views/tripMain.vue'),
    meta: {
      layout: 'AppLayoutMain',
    },
  },
	{
    path: '/login',
    name: 'loginMain',
    component: () => import('@/views/loginMain.vue'),
    meta: {
      layout: 'AppLayoutDefault',
			title: '로그인'
    },
  },  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router